import React from "react";
import { View, Text, Pressable, Dimensions } from "react-native";
import ApnaCollege from "./apnaCollege/index";
import SabkaPlayer from "./apnaCollege/player";
import { createBrowserRouter, RouterProvider, useNavigate } from "react-router-dom";

const d = Dimensions.get("window");

const Home = () => {
  const navigate = useNavigate();
  const nav = (type) => {
    navigate("apnaCollege/" + type, {
      type,
    });
  };
  return (
    <View style={{ height: d.height, backgroundColor: "#000000" }}>
      <Pressable
        onPress={() => nav("JAVA")}
        style={{
          height: 70,
          justifyContent: "center",
          backgroundColor: "#242424",
          paddingLeft: 20,
        }}
      >
        <Text style={{ fontSize: 20, color: "#c7c7c7", fontWeight: "bold" }}>
          Java Course
        </Text>
      </Pressable>
      <Pressable
        onPress={() => nav("WEB")}
        style={{
          height: 70,
          justifyContent: "center",
          backgroundColor: "#242424",
          paddingLeft: 20,
        }}
      >
        <Text style={{ fontSize: 20, color: "#c7c7c7", fontWeight: "bold" }}>
          Web Course
        </Text>
      </Pressable>
    </View>
  );
};

const router = createBrowserRouter([
  {
    path: "/",
    element: <Home />,
  },
  {
    path: "/apnaCollege/:type",
    element: <ApnaCollege />,
  },
  {
    path: "/sabkaPlayer/:id/:title",
    element: <SabkaPlayer />,
  },
],{
    basename: "/detoxweb",
  });


function App() {
  return <RouterProvider router={router} />;
}

export default App;