import React, { useState, useMemo } from "react";
import {
	View,
	Pressable,
	Text,
	FlatList,
	useWindowDimensions,
} from "react-native";
import webCourse from "./web.json";
import javaDSACourse from "./course.json";
import SabkaPlayer from "./player";

import { useParams, useNavigate } from "react-router-dom";
const ApnaCollege = ({ route }) => {
	const { height } = useWindowDimensions();
	const navigate = useNavigate();
	const courseTypeData = useParams();
	const [currentVideo, setCurrentVideo] = React.useState(false);

	const getCourseData = ({ type }) => {
		if (type === "WEB") {
			return webCourse;
		}
		return javaDSACourse;
	};
	const courses = useMemo(() => {
		const courseData = getCourseData(courseTypeData);
		const course = courseData.course;
		const sectionMap = course.sections;
		const sectionKeys = Object.keys(course.sections);
		const videos = courseData.course.videos;
		const objects = courseData.course.objects;
		const sections = sectionKeys.map((key) => {
			const section = sectionMap[key];
			const data = [];
			(section.learningPath || []).forEach((path) => {
				if (path.type === "ivideo" && videos[path.id]) {
					data.push(videos[path.id]);
				} else if (objects[path.id]) {
					data.push(objects[path.id]);
				}
			});
			section.id = key;
			section.data = data || [];
			return section;
		});
		return sections;
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);
	const [toggleMap, setToggleMap] = useState({});
	const [resetList, setResetList] = useState(false);

	const viewPath = (source) => {
		if (source.objectType === "ivideo") {
			const id = source.sourceid;
			// const playerUrl = `https://fast.wistia.com/embed/medias/${id}.m3u8`;
			setCurrentVideo(null);

			setTimeout(() => {
				setCurrentVideo({
					id,
					title: source.title,
				});
			}, 500);
		} else {
			alert("Only Video Supported Currently");
			console.log(source.data.pdf_full);
		}
	};

	const togglePath = (id) => {
		if (toggleMap[id]) {
			delete toggleMap[id];
		} else {
			toggleMap[id] = true;
		}
		setToggleMap(toggleMap);
		setResetList(!resetList);
	};

	const handleExit = () => {
		navigate(-1);
	};

	const renderCard = ({ item }) => {
		const isOpen = toggleMap[item.id];
		return (
			<View>
				<Pressable
					style={style.card}
					onPress={() => togglePath(item.id)}
				>
					<Text style={style.cardText}>{item.title}</Text>
					<View style={style.arrowCover}>
						<Text style={style.arrow}>{isOpen ? "-" : "+"}</Text>
					</View>
				</Pressable>
				{isOpen
					? item.data.map((path) => {
							const isCurrent = path.sourceid === currentVideo?.id;
							return (
								<Pressable
									key={path.id}
									onPress={() => viewPath(path)}
									style={[
										style.pathCover,
										{
											backgroundColor: isCurrent
												? "#feb420"
												: "#242424",
										},
									]}
								>
									<Text style={[style.pathText, { color: isCurrent ? "#000000" : "#c7c7c7" }]}>
										{path.title}
									</Text>
								</Pressable>
							);
					  })
					: null}
			</View>
		);
	};

	return (
		<View style={style.main}>
			<View style={[style.sideBar, { height }]}>
				<View style={style.header}>
					<Text style={style.headerText}>
						Sabka College 3.0 | {courseTypeData.type}
					</Text>
					<Text onPress={handleExit} style={style.closeText}>
						Exit
					</Text>
				</View>
				<FlatList
					showsVerticalScrollIndicator={false}
					refresh={!resetList}
					renderItem={renderCard}
					data={courses}
				/>
			</View>
			<View style={style.videoContent}>
				{currentVideo ? (
					<SabkaPlayer {...currentVideo} />
				) : (
					<Text style={style.empty}>Please select video</Text>
				)}
			</View>
		</View>
	);
};

const style = {
	videoContent: {
		flex: 1,
		justifyContent: "center",
		alignItems: "center",
	},
	empty: {
		textAlign: "center",
		color: "#c7c7c7",
	},
	sideBar: {
		width: 300,
		borderRightWidth: 1,
		borderColor: "#242424",
		height: 200,
	},
	main: {
		backgroundColor: "#000000",
		flex: 1,
		flexDirection: "row",
	},
	header: {
		alignItems: "center",
		justifyContent: "space-between",
		height: 50,
		paddingHorizontal: 10,
		borderBottomWidth: 1,
		borderColor: "#242424",
		flexDirection: "row",
	},
	headerText: {
		fontWeight: "bold",
		fontSize: 18,
		color: "#c7c7c7",
	},
	closeText: {
		fontWeight: "bold",
		fontSize: 15,
		color: "#c7c7c7",
	},
	card: {
		height: 50,
		paddingHorizontal: 10,
		flexDirection: "row",
		alignItems: "center",
		justifyContent: "space-between",
	},
	cardText: {
		fontSize: 16,
		color: "#c7c7c7",
		fontWeight: "500",
	},
	arrowCover: {
		height: 25,
		justifyContent: "center",
	},
	arrow: {
		fontSize: 15,
		color: "#c7c7c7",
	},
	pathCover: {
		height: 40,
		justifyContent: "center",
		backgroundColor: "#242424",
		borderBottomWidth: 1,
		borderColor: "#000000",
		paddingHorizontal: 10,
	},
	pathText: {
		fontSize: 13,
		color: "#c7c7c7",
	},
};

export default ApnaCollege;